}

.slider-range-container {
  padding-top: 50px;

  .slider-range {
    background: #E8E8E8;
    border-radius: 5px;
    border: 0 !important;
    height: 9px;

    .ui-slider-range {
      background: #009831;
      border-radius: 5px;
      border: 0;
      height: 9px;
    }

    .ui-slider-handle {
      background: #009833;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.50);
      border-radius: 50%;
      border: 0;
      top: -6px;

      span {
        position: absolute;
        top: -30px;
        color: #000;
      }
    }

    span.ui-slider-handle:first-child > span {
      left: 5px;
      background-color: #0a58ca;
    }

    span.ui-slider-handle:last-child > span {
      left: -10px;
    }

  }

  .sliderLabels {
    position: relative;
    height: 35px;
    margin-top: 5px;

    .slider-label {
      position: absolute;
      width: 25px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      i {
        font-style: normal;
        font-weight: 100;
        font-size: 12px;
      }
    }
  }
}

