body::-webkit-scrollbar:vertical {
    width: 12px;
  }

body::-webkit-scrollbar:horizontal {
    height: 12px;
  }

body::-webkit-scrollbar-thumb {
    background-color: rgb(0, 121, 76);
    border-radius: 10px;
    border: 2px solid #ffffff;
  }

body  ::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #ffffff;
  }

