.custom-chip-checks {
  display: flex;
  flex-wrap: wrap;

  .form-check {
    padding-left: 0;
    margin-right: 10px;
    margin-bottom: 10px;

    label {
      cursor: pointer;
    }
  }

  input[type="checkbox"] {
    display: none;
  }

  input[type="radio"] {
    display: none;
  }

  input[type="checkbox"] + label, input[type="radio"] + label {
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    font-family: Rubik-Regular;
    font-size: 15px;
    color: #4E5156;
    text-align: center;
    background: #FFFFFF;
    border: 1px solid #202330;
    border-radius: 25px;
    padding: 9px 15px;
    line-height: 1;
  }


  input[type="checkbox"]:checked + label, input[type="radio"]:checked + label {
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    background-color: #018E37;
    color: white;
    border: 1px solid #018E37;
    border-radius: 18px;
  }

}



